import styled from 'styled-components';

const StyledSelect = styled.select`
  font-family: 'Montserrat',
  sans-serif;
  font-size: .8em;
  background: none;
  border: 1px solid #fff;
  border-radius: 8px;
  padding: 2px 5px;

  option{
    text-align: center;
  }
`;

export default StyledSelect;